:root {
  --primary: #1b0a08;
}

/* .btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
} */

.btn--primary {
  /* background-image: url(/images/kente.jpg); */
  background-size: contain;
  background-color: var(--primary);
  color: #b1b1b1;
  width: 150px  auto;
  border: 1px solid #b17b4b;
}

.btn--outline {
  background-color: transparent;
  color: #b17b4b;
    text-shadow: 0px 1px #242424;
  padding: 8px 20px;
  border: 1px solid #b17b4b;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  -webkit-transition: 250ms;
  -o-transition: 250ms;
  transition: 250ms;
}



