.slideShow{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-image: url(/images/kente.jpg);
}


.slideshow-container {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  }
  
 

  .slide-image{
 
    height: 100%;
    /* border-radius: 10px; */
  }
  
  /* Next & previous buttons */
  .right-arrow {
    position: absolute;
    margin-top: 26%;
    right: 32px;
    font-size: 3rem;
    color: rgb(255, 255, 255);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    margin-top: 26%;
    left: 32px;
    font-size: 3rem;
    color: rgb(255, 255, 255);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  /* On hover, add a black background color with a little bit see-through */
  .right-arrow:hover, .left-arrow:hover {
    background-color: rgba(0,0,0,0.8);
    color: #a47d5a;
  }

  .slide {
    /* opacity: 0; */
    -webkit-transition-duration: 1s ease;
         -o-transition-duration: 1s ease;
            transition-duration: 1s ease;
  }
  
  .slide.active {
    /* opacity: 1; */
    display: -ms-flexbox;
    display: flex;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
    /* transform: scale(1.08); */
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    margin-left: -5px;
  }


  @media screen and (max-width: 768px){
    .right-arrow{
      margin-top: 30vh;
    }
    
    .left-arrow{
      margin-top: 30vh;
    }

    .slide.active {
          height: 70vh;
        }

        .slide-image {
          margin-top: 85px;
          width: 100%;
          max-height: 50vh;
          
      }
  



  }
  


 

  


